import { styled } from 'styled-components';

export const Container = styled.section`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

export const SearchContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  gap: ${({ theme }) => theme.spacing.l};
  background-color: ${({ theme }) => theme.colors.wrapperBackground};
  padding: ${({ theme }) => theme.spacing.m};
  margin: ${({ theme }) => theme.spacing['3xl']} 0;
  border-radius: 5px;
  width: 100%;
  max-width: ${({ theme }) => theme.size.elementSize.s};

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    flex-direction: column;
  }
`;

export const MapWrapper = styled.div`
  width: 100%;
  height: ${({ theme }) => theme.size.elementSize.m};
  border-radius: 10px;

  @media ${({ theme }) => theme.mediaQuery.tablet} {
    height: ${({ theme }) => theme.size.elementSize.s};
  }

  @media ${({ theme }) => theme.mediaQuery.mobile} {
    height: ${({ theme }) => theme.size.elementSize.xs};
  }
`;

export const Subscription = styled.h3`
  font-size: ${({ theme }) => theme.font.fontSizes.xl};
  font-weight: ${({ theme }) => theme.font.fontWeights.normal};
  text-align: center;
  text-transform: capitalize;

  margin-top: ${({ theme }) => theme.spacing.xl};
`;
