import { Component } from 'react';
import { MemoizedCurrencySearch as CurrencySearch } from '@components/CurrencySearch';
import { MainLoader } from '@components/Loaders/MainLoader';
import { CustomMarker } from '@components/UI/Map/CustomMarker';
import { CustomPopup } from '@components/UI/Map/CustomPopup';
import { API_CONFIG } from '@constants/config';
import { fetchBanksThunk, setSelectedCurrency } from '@store/slices/bankSlice';
import ReactMapGL, { ViewStateChangeEvent } from 'react-map-gl';
import { connect } from 'react-redux';

import { RootState } from '@/store';

import {
  fixedCoordinates,
  mapConfig,
  markerImages,
  searchContainerSubscription,
  searchRadius,
} from './config';
import { Container, MapWrapper, SearchContainer, Subscription } from './styled';
import { BanksMapProps, BanksMapState } from './types';

import 'mapbox-gl/dist/mapbox-gl.css';

const { initialZoom, mapStyle, bearing, pitch, padding } = mapConfig;
const { fixedLatitude, fixedLongitude } = fixedCoordinates;

class BanksMap extends Component<BanksMapProps, BanksMapState> {
  state: BanksMapState = {
    viewport: {
      latitude: fixedLatitude,
      longitude: fixedLongitude,
      zoom: initialZoom,
      bearing,
      pitch,
      padding,
    },
    userLocation: { lat: fixedLatitude, lng: fixedLongitude },
    searchRadius,
    selectedBank: null,
  };

  async componentDidMount() {
    if (this.props.banks.length > 0) {
      this.centerMapOnUser();
    } else {
      this.fetchBanks();
    }
  }

  componentDidUpdate(prevProps: BanksMapProps) {
    if (prevProps.selectedCurrency !== this.props.selectedCurrency) {
      this.fetchBanks();
    }
  }

  fetchBanks = () => {
    const { userLocation, searchRadius } = this.state;
    const { selectedCurrency } = this.props;

    this.props.fetchBanksThunk({
      location: userLocation,
      radius: searchRadius,
      currency: selectedCurrency,
    });
  };

  centerMapOnUser = () => {
    const { lat: latitude, lng: longitude } = this.state.userLocation;
    this.setState((prevState) => ({
      viewport: { ...prevState.viewport, latitude, longitude },
    }));
  };

  handleCurrencyChange = (currency: string | null) => {
    this.props.setSelectedCurrency(currency);
  };

  handleMarkerClick = (bankId: string) => () => {
    const selectedBank =
      this.props.banks.find((bank) => bank.id === bankId) || null;
    this.setState({ selectedBank });
  };

  handleViewportChange = (event: ViewStateChangeEvent) => {
    const { latitude, longitude, zoom } = event.viewState;
    this.setState((prevState) => ({
      viewport: { ...prevState.viewport, latitude, longitude, zoom },
    }));
  };

  closePopup = () => {
    this.setState({ selectedBank: null });
  };

  render() {
    const { viewport, userLocation, selectedBank } = this.state;
    const { banks, isLoading } = this.props;
    return (
      <Container>
        <Subscription>{searchContainerSubscription}</Subscription>
        <SearchContainer>
          <CurrencySearch
            excludedCurrency=""
            defaultCurrency={this.props.selectedCurrency}
            onCurrencyChange={this.handleCurrencyChange}
          />
        </SearchContainer>

        <MapWrapper>
          {isLoading ? (
            <MainLoader />
          ) : (
            <ReactMapGL
              {...viewport}
              mapboxAccessToken={API_CONFIG.MAPBOX_TOKEN}
              onMove={this.handleViewportChange}
              mapStyle={mapStyle}
            >
              {userLocation && (
                <CustomMarker
                  latitude={userLocation.lat}
                  longitude={userLocation.lng}
                  imageUrl={markerImages.user}
                />
              )}

              {banks.map(({ id, location: { lat, lng } }) => (
                <CustomMarker
                  key={id}
                  latitude={lat}
                  longitude={lng}
                  onClick={this.handleMarkerClick(id)}
                  imageUrl={markerImages.bank}
                />
              ))}

              {selectedBank && (
                <CustomPopup
                  selectedBank={selectedBank}
                  onClose={this.closePopup}
                />
              )}
            </ReactMapGL>
          )}
        </MapWrapper>
      </Container>
    );
  }
}

const mapStateToProps = (state: RootState) => ({
  banks: state.banks.banks,
  selectedCurrency: state.banks.selectedCurrency,
  isLoading: state.banks.isLoading,
});

const mapDispatchToProps = {
  fetchBanksThunk: fetchBanksThunk,
  setSelectedCurrency,
};

export default connect(mapStateToProps, mapDispatchToProps)(BanksMap);
