import { Popup } from 'react-map-gl';

import { Container, Text, Title } from './styled';
import { CustomPopupProps } from './types';

export const CustomPopup = ({ selectedBank, onClose }: CustomPopupProps) => {
  const {
    location: { lat, lng },
    name,
    address,
    currencySupport,
  } = selectedBank;
  return (
    <Popup
      latitude={lat}
      longitude={lng}
      onClose={onClose}
      closeButton={true}
      closeOnClick={false}
    >
      <Container>
        <Title>{name}</Title>
        <Text>Address: {address}</Text>
        <Text>Currencies: {currencySupport.join(', ')}</Text>
      </Container>
    </Popup>
  );
};
