import { Marker } from 'react-map-gl';

import { MarkerImage } from './styled';
import { CustomMarkerProps } from './types';

export const CustomMarker = (props: CustomMarkerProps) => {
  const { latitude, longitude, onClick, imageUrl } = props;
  return (
    <Marker latitude={latitude} longitude={longitude} onClick={onClick}>
      <MarkerImage src={imageUrl} />
    </Marker>
  );
};
